.section {
  position: relative;
  z-index: 1;
  padding: 2rem 0 6rem;
  margin: 0 auto;
  background-color: $body-bg;
  background-image: linear-gradient(-180deg, $body-bg 0%, $gray-400 97%);

  h3 {
    margin: 2rem 0;
  }
}

.section-page-top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 80vh;
  padding: 0;
  background-position: center;
  background-size: cover;

  @include media-breakpoint-up(lg) {
    min-height: 40rem;
  }
}

.section-blue-bg {
  background-color: $blue;
  background-image: linear-gradient(-150deg, $blue 0%, $darkblue 100%);
}

.section-blue-bg-bordered {
  padding: 0 2rem;
  box-shadow: 0 0 20px 0 transparentize($black, .75);

  .row {
    @include media-breakpoint-down(md) {
      margin-right: -$grid-gutter-width;
      margin-left: -$grid-gutter-width;
    }
  }

  .col {
    padding: 2rem 3.25rem 3rem;
    border: 0 solid transparentize($white, .5);

    &:last-child {
      border: none;
    }

    @include media-breakpoint-down(md) {
      border-bottom-width: 1px;
    }

    @include media-breakpoint-up(lg) {
      border-right-width: 1px;
    }
  }

  .col-icon-top {
    padding-top: 6.2rem;

    &::before {
      top: 2rem;
    }

    &.col-icon-lg {
      height: 8rem;
    }
  }
}

.section-img-bg {
  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
  transform: translateY(-50%);
}

.col-spread {
  display: flex;
  flex-direction: column;

  * {
    flex: 0 0 auto;
  }

  p {
    flex: 1 1 auto;
  }
}

.col-icon {
  position: relative;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
}

.col-icon-left {
  padding-left: 5rem;

  &::before {
    width: 3.25rem;
    height: 3.25rem;
  }
}

.col-icon-top {
  padding-top: 4.2rem;

  &::before {
    right: 0;
    height: 4.2rem;
  }
}

.col-icon-lg {
  .col-icon-top {
    padding-top: 6rem;

    &::before {
      height: 6rem;
    }
  }
}

.section-img {
  background-position: center;
  background-size: cover;
}

.bg-image-1 {
  background-image: url("../img/isv-bg-1.jpg");
}

.bg-image-2 {
  background-image: url("../img/isv-bg-2.jpg");
}

.bg-image-3 {
  background-image: url("../img/isv-bg-3.jpg");
}

.bg-image-doubleverify {
  background-image: url("../img/doubleverify.jpg");
}


.col-numbered {
  &::before {
    position: absolute;
    top: -2rem;
    left: -.8rem;
    z-index: -1;
    font-family: $font-family-heavy;
    font-size: 8.75rem;
    color: transparentize($white, .9);
  }

  @for $i from 1 through $grid-columns {
    &:nth-child(#{$i})::before {
      content: "#{$i}";
    }
  }
}

.col-bg {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.col-12 {
  @include media-breakpoint-down(md) {
    padding-bottom: $grid-gutter-width * 4 / 3;
  }
}

.col-bg-zendesk {
  background-image: url("../img/zendesk.png");
}

.col-bg-forrester {
  background-image: url("../img/forrester.png");
}
